<template>
    <div class="intro-y box">
        <div class="flex flex-col sm:flex-row items-center p-5 border-b border-slate-200/60 dark:border-darkmode-400">
            <h2 class="font-medium text-base mr-auto">
                Edit User Form
            </h2>
        </div>
        <div id="vertical-form" class="p-5">
            <div class="preview">
                <form v-on:submit.prevent="onSubmit" method="post">
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">First Name</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="First Name"
                            v-model="posts.firstName" name="firstName">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Last Name</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="Last Name"
                            v-model="posts.lastName" name="lastName">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Email</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="email@gmail.com"
                            v-model="posts.email">

                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Mobile Number</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="Mobile Number"
                            v-model="posts.phoneNumber" name="phoneNumber">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Password</label>
                        <input id="vertical-form-2" class="form-control" type="password" v-model="posts.password"
                            name="password">
                        <div class="mt-3">
                            <label for="vertical-form-2" class="form-label">Date of Year</label>
                            <input id="vertical-form-2" type="text" class="form-control" v-model="posts.year" name="year" />
                            <!-- <p v-show="!year" class="text-danger">Date of Year is required.</p> -->
                        </div>
                        <div class="mt-3">
                            <label for="vertical-form-2" class="form-label">Months of Birth</label>
                            <select v-model="posts.months"
                                class="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                                <option value="">Select a month</option>
                                <option v-for="(month, index) in monthName" :key="index" :value="month">{{ month }}
                                </option>
                            </select>
                        </div>
                        <div class="mt-3">
                            <label for="vertical-form-2" class="form-label">Country</label>
                            <input id="vertical-form-2" type="text" class="form-control" v-model="posts.country"
                                name="country" />
                        </div>
                    </div>
                    <button class="btn btn-primary mt-5" type="submit">Edit</button>
                </form>
            </div>
        </div>
    </div>
</template>


<script>
import { getRequestById, putRequest } from '../../pages/ApiFunctions';

export default {
    name: "EditUser",
    data() {
        return {
            monthName: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
            userId: '',
            posts: {
                firstName: "",
                lastName: "",
                email: "",
                phoneNumber: "",
                password: '',
                months: '',
                country: '',
                year: ''

            }
        }
    },
    mounted() {
        this.userId = this.$route.params.id
        this.getUserById(this.$route.params.id)
    },
    methods: {
        async onSubmit() {
            const res = await putRequest(`/update-user/${this.userId}`, this.posts)
            console.log("es=>", res);
            this.$router.push({ name: 'UserDataList', })

        },

        async getUserById(id) {
            const res = await getRequestById(`/get-user/id/${id}`)
            this.posts.email = res.data.email
            this.posts.firstName = res.data.firstName
            this.posts.lastName = res.data.lastName
            this.posts.phoneNumber = res.data.phoneNumber
            this.posts.months = res.data.months,
                this.posts.year = res.data.year,
                this.posts.country = res.data.country

        },

    },

}
</script>



